html {
  width: 100%;
}

body {
  margin: 0;
  min-height: 100%; 
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--slate);
  font-size: var(--fz-xl);
  line-height: 1.3;
}


:root {
  --text-primary:#1523f7;
  --text-secondary:#979797;
  --slate: #b3b3b3;
  --light-slate: #a8b2d1;
  --primary-color:#d90200;
  --primary:#041630;
  --background:#041630;
  
  --dark-navy: #121cad;
  --navy: #0a192f;
  --light-navy: #112240;
  --lightest-navy: #233554;
  --navy-shadow: rgba(2, 12, 27, 0.7);
  --slate: #b3b3b3;
  --light-slate: #a8b2d1;
  --lightest-slate: #ccd6f6;
  --white: #e6f1ff;
  --green: #64ffda;
  --green-tint: rgba(100, 255, 218, 0.1);


  --font-sans: Poppins;
  --font-mono: Poppins;

  --fz-xxs: 12px;
  --fz-xs: 13px;
  --fz-sm: 14px;
  --fz-md: 16px;
  --fz-lg: 18px;
  --fz-xl: 20px;
  --fz-xxl: 22px;
  --fz-heading: 32px;


    --fz-xxs: 12px;
    --fz-xs: 13px;
    --fz-sm: 14px;
    --fz-md: 16px;
    --fz-lg: 18px;
    --fz-xl: 20px;
    --fz-xxl: 22px;
    --fz-heading: 32px;

    --border-radius: 4px;
    --nav-height: 100px;
    --nav-scroll-height: 70px;

    --tab-height: 42px;
    --tab-width: 120px;

    --easing: cubic-bezier(0.645, 0.045, 0.355, 1);
    --transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);

    --hamburger-width: 30px;

    --ham-before: top 0.1s ease-in 0.25s, opacity 0.1s ease-in;
    --ham-before-active: top 0.1s ease-out, opacity 0.1s ease-out 0.12s;
    --ham-after: bottom 0.1s ease-in 0.25s, transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    --ham-after-active: bottom 0.1s ease-out, transform 0.22s cubic-bezier(0.215, 0.61, 0.355, 1) 0.12s;
}

/* Text Layout */
.button {
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.button-primary {
  background-color: rgba(255, 255, 255, 0); 
  color: rgb(255, 255, 255); 
  border: 2px solid var(--primary-color);
}

.button-primary:hover {
  background-color: var(--primary-color);
  color: white;
}

.button-primary-second {
  background-color: rgba(255, 255, 255, 0); 
  color: var(--primary-color); 
  border: 2px solid var(--primary-color);
}

.button-primary-second:hover {
  background-color: var(--primary-color);
  color: white;
}
.txt_info{
  font-family: var(--font-sans);
  color: var(--primary-color);
  margin: 0px 0px 30px 4px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.1;
}

.txt_primary{
  font-weight: 600;
  color: var(--text-primary);
  margin: 0px;
  font-size: clamp(40px, 8vw, 80px);
  font-family: var(--font-sans);
}

.txt_secondary{
  margin-top: 10px;
  color: var(--primary-color);
  line-height: 0.9;
  font-size: clamp(40px, 8vw, 80px);
  font-weight: 600;
  font-family: var(--font-sans);
}

.txt_verse{
  color: var(--background);
  margin-top: 5%;
  font-size: 25px;
  line-height: 1.3;
  font-family: var(--font-sans);
}

.txt_reference_verse{
  color: var(--background);
  font-size: 18px;
  color: var(--primary-color);
  font-family: Poppins;
}
/* Navigation */


header.masthead {
  padding-top: 10rem;
  padding-bottom: calc(10rem - 56px);
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

header.masthead hr {
  margin-top: 30px;
  margin-bottom: 30px;
}

@media (min-width: 992px) {
  header.masthead {
    height: 100vh;
    min-height: 650px;
    padding-top: 0;
    padding-bottom: 0;
  }
}

section {
  padding: 8rem 0;
}

.section-heading {
  margin-top: 0;
}

.main_container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.about_me_section{
  background-color: var(--background);
}

.about_me{
  align-items: center; 
  justify-content: center;
  text-align: center;
}

.about_me_sbutitle{
  color: #ff7057;
}

.about_me_description{
  margin-left: 5%;
  margin-right: 5%;
}

.meetings_section{
  
}

.meetings_me{
  align-items: center; 
  justify-content: center;
  text-align: center;
}
.meetings_me_items{
